import React, { Component } from "react"
import { Link, navigate } from "gatsby"
import { logout, isLoggedIn } from "../utils/auth"
import { Navbar, NavbarBrand, NavbarItem, Icon, NavbarBurger, NavbarMenu, NavbarStart,
NavbarLink, NavbarDropdown, NavbarEnd, Field, Control, Button } from 'bloomer';

class NavbarApp extends Component {

  constructor(props) {
    super(props);

    this.state = {
      isActive: false
    }

    this.onClickNav = this.onClickNav.bind(this);
    this.handleLogout = this.handleLogout.bind(this);
  }

  handleLogout(e){
    e.preventDefault();
    logout(() => {
      navigate('/login')
    })
  }

  onClickNav(){
    this.setState({ isActive: !this.state.isActive})
  }

  render () {
    if(!isLoggedIn()) return (<></>)

    return (
      <Navbar style={{ borderBottom: 'solid 1px #CCC', margin: '0'}}>
        <NavbarBrand>
            <NavbarItem>
                <img src="https://bloomer.js.org/dist/07990594.png" alt="" style={{ marginRight: 5 }} /> Bloomer
            </NavbarItem>
            <NavbarItem isHidden='desktop'>
                <Icon className='fa fa-github' />
            </NavbarItem>
            <NavbarItem isHidden='desktop'>
                <Icon className='fa fa-twitter' style={{ color: '#55acee' }} />
            </NavbarItem>
            <NavbarBurger isActive={this.state.isActive} onClick={this.onClickNav} />
        </NavbarBrand>
        <NavbarMenu isActive={this.state.isActive} onClick={this.onClickNav}>
            <NavbarStart>
                <Link className="navbar-item" to="/">Home</Link>
                <NavbarItem hasDropdown isHoverable>
                    <NavbarLink>Data</NavbarLink>
                    <NavbarDropdown>
                      <Link className="navbar-item" to="/user">Mahasiswa</Link>
                    </NavbarDropdown>
                </NavbarItem>
            </NavbarStart>
            <NavbarEnd>
                <NavbarItem href="/logout" onClick={(e) => this.handleLogout(e) } isHidden='touch'>
                    Logout
                </NavbarItem>
            </NavbarEnd>
        </NavbarMenu>
      </Navbar>
    )
  }
}


export default NavbarApp
