import { handleSuccess, handleError, authHeader, authHeaderUrlEncoded, config } from '../../utils/';
import axios from 'axios';

export const userService = {
  getAll,
  get,
  save,
  remove
};

function getAll() {
  return axios.get(`${config.apiUrl}/api/user`, { headers: authHeader() }).then(handleSuccess).catch(handleError)
}

function get(id) {
  return axios.get(`${config.apiUrl}/api/user/${id}`, { headers: authHeader() }).then(handleSuccess).catch(handleError)
}

function save(data, id) {
  let formData = new FormData();
  for (let key in data) {
    formData.append(key, data[key]);
  }
  return axios.post(`${config.apiUrl}/api/user/${id}`, formData, { headers: authHeaderUrlEncoded() }).then(handleSuccess).catch(handleError)
}

function remove(id) {
  return axios.delete(`${config.apiUrl}/api/user/${id}`, { headers: authHeader() }).then(handleSuccess).catch(handleError)
}
