import { userConstants, appConstants } from '../constants'
import { navigate } from 'gatsby';
import { authService } from '../services'
import { setUser } from '../../utils/auth'
import { toastr } from 'react-redux-toastr'
export const authActions = {
  login,
  logout,
  register
};

function login(data) {
  let payload = { data }
  return dispatch => {
    dispatch({ type: userConstants.LOGIN_REQUEST, payload })
    authService.login(data)
      .then(
          res => {
              dispatch({ type: userConstants.LOGIN_SUCCESS, res })
              toastr.success(appConstants.NOTIF_TITLE_SUCCESS, res.message)
              setUser(res)
              navigate('/')
          },
          error => {
            dispatch({ type: userConstants.LOGIN_FAILURE, error: error.toString() })
            toastr.error(appConstants.NOTIF_TITLE_ERROR, error.toString())
          }
      );
  }
}

function logout() {
  setUser({})
  navigate('/login')
  return { type: userConstants.LOGOUT };
}

function register(data) {
  let payload = { data }
  return dispatch => {
    dispatch({ type: userConstants.REGISTER_REQUEST, payload })
    authService.register(data)
      .then(
          res => {
              dispatch({ type: userConstants.REGISTER_SUCCESS, res })
              toastr.success(appConstants.NOTIF_TITLE_SUCCESS, res.message)
          },
          error => dispatch({ type: userConstants.REGISTER_FAILURE, error: error.toString() })
      );
  }
}
