import { userConstants, appConstants } from '../constants';
import { userService } from '../services';
import { toastr } from 'react-redux-toastr'
export const userActions = {
  getAll,
  get,
  save,
  remove
};

function getAll() {
  return dispatch => {
    dispatch({ type: userConstants.GETALL_REQUEST })
    userService.getAll()
      .then(
          res => {
              dispatch({ type: userConstants.GETALL_SUCCESS, res })
          },
          error => dispatch({ type: userConstants.GETALL_FAILURE, error: error.toString() })
      );
  }
}

function get(id) {
  return dispatch => {
    dispatch({ type: userConstants.GET_REQUEST, id })
    userService.get(id)
      .then(
          res => {
              dispatch({ type: userConstants.GET_SUCCESS, res })
          },
          error => dispatch({ type: userConstants.GET_FAILURE, error: error.toString() })
      );
  }
}

function save(data, id) {
  let payload = { data, id }
  return dispatch => {
      dispatch({ type: userConstants.SAVE_REQUEST, payload })
      userService.save(data, id)
        .then(
            res => {
                dispatch({ type: userConstants.SAVE_SUCCESS, res })
                toastr.success(appConstants.NOTIF_TITLE_SUCCESS, res.message)
            },
            error => {
                dispatch({ type: userConstants.SAVE_FAILURE, error: error.toString() })
                toastr.error(appConstants.NOTIF_TITLE_ERROR, error.toString())
            }
      );
  }
}

function remove(id) {
  let payload = { id }
  return dispatch => {
      dispatch({ type: userConstants.DELETE_REQUEST, payload })
      userService.remove(id)
      .then(
          res => {
              dispatch({ type: userConstants.DELETE_SUCCESS, res })
              toastr.success(appConstants.NOTIF_TITLE_SUCCESS, res.message)
              dispatch(getAll())
          },
          error => {
              dispatch({ type: userConstants.DELETE_FAILURE, error: error.toString() })
              toastr.error(appConstants.NOTIF_TITLE_ERROR, error.toString())
          }
      );
  }
}
