import { handleSuccess, handleError, authHeader, config } from '../../utils/';
import axios from 'axios';

export const authService = {
  login,
  register
};

function login(data) {
  return axios.post(`${config.apiUrl}/api/login`, data, { headers: authHeader() }).then(handleSuccess).catch(handleError)
}

function register(data) {
  let formData = new FormData();
  for (let key in data) {
    formData.append(key, data[key]);
  }
  return axios.post(`${config.apiUrl}/api/register`, formData, { headers: authHeader() }).then(handleSuccess).catch(handleError)
}
